@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;500&family=Poppins:wght@500;800&display=swap');

/* removing default body margin */
body {
   margin: 0px;
}

.glassmorphism {
   background: rgba(86, 67, 253, 0.3);
   box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
   backdrop-filter: blur(4px);
   -webkit-backdrop-filter: blur(7px);
}