@keyframes rocket-landing {
   0% {
      transform: translateX(100vw) rotate(0deg);
   }

   50% {
      transform: translateX(50vw) rotate(180deg);
   }

   100% {
      transform: translateX(0px) rotate(0deg);
   }
}

@keyframes fade {
   from {
      opacity: 0%;
   }

   to {
      opacity: 100%;
   }
}

@keyframes levitate {
   0% {
      transform: translateY(0);
   }

   50% {
      transform: translateY(-16px);
   }

   100% {
      transform: translateY(0px);
   }
}

.title-main {
   display: flex;
   justify-content: center;
   align-items: center;
   width: calc(100% - 25rem);
   height: 80vh;
   position: relative;
   left: 25rem;
}

.title-container {
   display: flex;
   flex-direction: column;
   align-items: center;
   animation: fade 1s;
}

.title-container>h1 {
   margin: 0px;
   font: 600 6.25rem "Poppins", sans-serif;
   color: #5643FD;
   animation: 2.5s ease 0s infinite normal levitate;
}

.title-container>p {
   margin: 0px;
   font: 200 1.75rem "Inter", sans-serif;
   text-transform: uppercase;
   color: #F5F3CE;

   position: relative;
   top: -32px;

   animation: 2.5s ease 0s infinite normal levitate;
}

.join-btn {
   align-self: center;
   font: 500 1.2rem "Poppins", sans-serif;
   text-transform: uppercase;
   padding: 8px 28px;
   color: #fff;
   background: #5643FD;
   outline: none;
   border: 0px;
   border-radius: 32px;
   transition: padding 0.5s;
}

.join-btn:hover {
   color: #5643FD;
   background: #F5F3CE;
   font: 800 1.2rem "Poppins", sans-serif;
   padding: 12px 32px;
}



.nakshatra-logo {
   animation: 2.5s ease 0s infinite normal levitate;
}

.rocket {
   animation: rocket-landing 2s;
}